.direction-form {
    position: block;
    width: 343px;
    height: 106px;
    background: #FFFFFF;
    box-shadow: 0px 7px 20px rgba(0, 0, 0, 0.07);
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.direction-form__label {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
    font-size: 15px;
    color: #6979F8;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 14px 0px 0px 0px;

}

.direction-form__icon {
    height: 24px;
    width: 24px;
    margin-left: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.direction-form__title {
    flex-grow: 1;
    font-size: 15px;
    line-height: 20px;
    height: 20px;
    margin-right: 8px;
    border: none;
    width: 220px;
    display: flex;
    align-items: center;
}

.direction-form__icons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 25px;

}

.direction-form__connect {
    width: 2px;
    height: 14px;
    margin-right: 4px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.oval__default {
    color: #E5E7FA;
    border: 1.5px solid #E5E7FA;
    border-radius: 1px;
    margin-bottom: 4px;
}

.direction-form__line {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 14px;
    margin-top: 1px;

}

.direction-form__arrows {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 32px;
    width: 32px;
    border-radius: 50%;
    background-color: #6979F8;
    color: #FFFFFF;
    margin: 0;
    cursor: pointer;
}

.direction-form__input {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
    display: flex;
    align-items: flex-start;
    margin-bottom: 14px;

}

.direction-form__icon_geo {
    color: linear-gradient(231.95deg, #BD7AE3 0%, #8461C9 100%);
}